import { apiEndPoints, fetchApi } from '../services/api';

export async function getMarketTicker() {
  try {
    console.log(process.env.REACT_APP_CMC_KEY);
    const response = await fetchApi(apiEndPoints.marketTicker);
    if (response.ok) {
      const json = await response.json();
      console.log(json);
    } else {
      console.log('Error in fetching!');
    }
  } catch (error) {
    console.log('Error in fetching!');
  }
}
